* {
  box-sizing: border-box;
}

body {
  font-family: sans-serif;
  margin: 20px;
  padding: 0;
}

/* Title page styles: */
.app {
  text-align: center;
}

.app label {
  font-size: 20px;
  color: #4cc0d2;
  font-style: bold;
}

.app select {
  font-size: 16px;
  padding: 7px;
  margin-left: 8px;
  color: #4cc0d2;
  border: none;
  font-weight: 550;
}

.app select option[value="game2048"] {
  color: #4e81f1;
  font-weight: 550;
}

.app select option[value="diceRoller"] {
  color: #16a34a;
  font-weight: 550;
}

.app select option[value="gomoku"] {
  font-weight: 550;
}

.app select option[value="ticTacToe"] {
  font-weight: 550;
}

/* -------------------------- Tic Tac Toe styles -------------------------- */
.ticGame {
  margin-top: 30px;
}

.ticHeader {
  color: #4cc0d2;
  margin-bottom: 10px;
}

.square {
  background: #f5f5da;
  border: 2px solid #dcb35c;
  float: left;
  font-size: 40px;
  font-weight: bold;
  line-height: 60px;
  height: 60px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 60px;
}

.board-row:after {
  clear: both;
  content: '';
  display: table;
}

.status {
  margin-bottom: 10px;
}

.game {
  display: flex;
  flex-direction: row;
}

.game-info {
  margin-left: 20px;
}

.square.win {
  background-color: rgba(34, 197, 94, 0.12);
  box-shadow: 0 0 12px rgba(34, 197, 94, 0.5);
  z-index: 1;
}

.game-info {
  font-style: oblique;
  color: #4e81f1;
}

.game-info button {
  background: #4cc0d2;
  border: none;
  border-radius: 6px;
  color: white;
  cursor: pointer;
  font-size: 14px;
  padding: 4px 10px;
  transition: background-color 0.2s ease-in-out;
  margin-top: 10px;
}

/* -------------------------- Gomoku styles -------------------------- */
.gomoku {
  display: block;
  margin-top: 30px;
  font-family: system-ui, -apple-system, Segoe UI, Roboto, Helvetica, Arial;
}

.gomoku_panel {
  display: flex;
  gap: 12px;
  align-items: center;
  margin-bottom: 12px;
}

.gomoku_panel strong {
  font-size: 18px;
}

.gomoku_board {
  display: grid;
  gap: 2px;
  background: #dcb35c;
  padding: 8px;
  border-radius: 12px;
  box-shadow: 0 6px 24px rgba(0, 0, 0, 0.15);
  user-select: none;
  width: max-content;
}

.gomoku_cell {
  background: linear-gradient(180deg, #e7c36a, #d1a348);
  border: 1px solid rgba(0, 0, 0, 0.15);
  border-radius: 6px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: transform 0.05s ease-out;
  line-height: 1;
}

.gomoku_cell:active {
  transform: scale(0.95);
}

.gomoku_cell.win {
  outline: 3px solid rgba(76, 192, 210, 0.6);
  background: linear-gradient(135deg, #afd45a, #f3d767);
}

.gomoku_operation {
  margin-bottom: 10px;
  display: flex;
  gap: 8px;
}

.gomoku_operation button {
  background: #4cc0d2;
  border: none;
  border-radius: 6px;
  color: white;
  cursor: pointer;
  font-size: 14px;
  padding: 8px 16px;
  transition: background-color 0.2s ease-in-out;
}

/* -------------------------- Dice roller styles -------------------------- */
.dice-container {
  margin-top: 30px;
  text-align: center;
}

.dice-instruction {
  margin-bottom: 30px;
  font-size: medium;
  font-style: italic;
}

.edit-buttons button,
.message-wrapper button,
.input-form button {
  background: #4cc0d2;
  border: none;
  border-radius: 6px;
  color: white;
  cursor: pointer;
  font-size: 14px;
  padding: 6px 8px;
  transition: background-color 0.2s ease-in-out;
  margin: 3px;
}

.dice-total {
  color: #16a34a;
}

.dice-rolls,
.dice-results {
  color: #4e81f1;
}

.dice-grand {
  color: #afd45a;
}

.dice-results {
  margin-bottom: 16px;
}

.messages {
  color: #3100b9;
}

.reset-btn {
  background: #5fdbee;
  position: fixed;
  right: 16px;
  bottom: 24px;
  z-index: 2;
  padding: 10px 14px;
  border-radius: 20px;
  border: none;
  cursor: pointer;
}

.reset-btn:hover {
  filter: brightness(0.95);
}

.quickbar {
  position: fixed;
  left: 8px;
  top: 50%;
  transform: translateY(-50%);
  display: flex;
  flex-direction: column;
  gap: 30px;
  z-index: 2;
}

.quick-btn {
  width: 50px;
  height: 50px;
  padding: 0;
  border: none;
  background-color: transparent;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  cursor: pointer;
}

.quick-btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.quick-btn:hover {
  filter: brightness(1.1);
}

.quick-btn:focus-visible {
  outline: 2px solid #4da3ff;
  outline-offset: 2px;
}


/* -------------------------- 2048 styles -------------------------- */
.instruction2048 {
  margin-top: 30px;
  color: #4e81f1;
  font-weight: bold;
}

.header2048 {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 10px;
  margin-top: 30px;
}

.title2048 {
  margin: 0;
  color: #4cc0d2;
}

.score2048 {
  font-weight: 700;
  color: #e9922f;
}

.best2048 {
  font-weight: 700;
  opacity: .9;
  color: #e9732f;
}

.btn2048 {
  background: #4cc0d2;
  padding: 6px 10px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  color: white;
  font-weight: 600;
}

.btn2048:hover {
  filter: brightness(1.1);
}

.undoButton2048 {
  margin-bottom: 10px;
  margin-left: 310px;
}

.undoButton2048:disabled {
  opacity: .5;
  cursor: not-allowed;
}

.grid2048 {
  grid-template-columns: repeat(var(--cols, 4), 1fr);
  display: grid;
  gap: 10px;
  background: #bbada0;
  padding: 10px;
  border-radius: 8px;
}

.cell2048 {
  width: 80px;
  height: 80px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 24px;
  font-weight: 800;
  border-radius: 6px;
  user-select: none;
}

.controls2048 {
  display: grid;
  grid-template-columns: repeat(3, 48px);
  grid-template-rows: repeat(3, 48px);
  gap: 20px;
  justify-content: center;
  align-items: center;
  margin-top: 30px;
}

.arrowBtn2048 {
  font-style: bold;
  font-size: 60px;
  padding: 0;
  background-color: transparent;
}

.GameOver2048 {
  color: rgb(158, 0, 0);
  font-size: 45px;
  font-weight: 900;
}

/* 2048 tile styles */
.cell2048.v0 {
  background: #cdc1b4;
  color: #776e65;
}

.cell2048.v2 {
  background: #eee4da;
  color: #776e65;
}

.cell2048.v4 {
  background: #ede0c8;
  color: #776e65;
}

.cell2048.v8 {
  background: #f2b179;
  color: #f9f6f2;
}

.cell2048.v16 {
  background: #f59563;
  color: #f9f6f2;
}

.cell2048.v32 {
  background: #f67c5f;
  color: #f9f6f2;
}

.cell2048.v64 {
  background: #f65e3b;
  color: #f9f6f2;
}

.cell2048.v128 {
  background: #edcf72;
  color: #f9f6f2;
}

.cell2048.v256 {
  background: #edcc61;
  color: #f9f6f2;
}

.cell2048.v512 {
  background: #edc850;
  color: #f9f6f2;
}

.cell2048.v1024 {
  background: #edc53f;
  color: #f9f6f2;
}

.cell2048.v2048 {
  background: #edc22e;
  color: #f9f6f2;
}

.cell2048.v4096 {
  background: #3c3a32;
  color: #f9f6f2;
}

.cell2048.v8192 {
  background: #8b6de6;
  color: #f9f6f2;
}

.cell2048.wild2048 {
  background: #887af1;
  color: #f9f6f2;
}

.boardWrap2048 {
  position: relative;
  display: inline-block;
}

.animLayer2048 {
  position: absolute;
  inset: 10px;
  /* must equal .grid2048's padding */
  pointer-events: none;
}

.movingOut2048 {
  visibility: hidden;
}

.movingTile2048 .movingInner2048 {
  position: absolute;
  width: 80px;
  /* must equal .cell2048's width and height */
  height: 80px;
  /* must equal .cell2048's width and height */
  transition: transform;
  transition-timing-function: ease-out;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 24px;
  font-weight: 800;
  border-radius: 6px;
  user-select: none;
}

@keyframes flash2048-kf {
  0% {
    box-shadow: 0 0 0 0 rgba(255, 255, 255, 0.85);
    transform: scale(1.0);
  }

  60% {
    box-shadow: 0 0 0 10px rgba(255, 255, 255, 0);
    transform: scale(1.06);
  }

  100% {
    box-shadow: none;
    transform: scale(1.0);
  }
}

.flash2048 {
  animation: flash2048-kf 30ms ease-out;
}

@keyframes spawn2048-kf {
  0% {
    transform: scale(0.6);
    opacity: 0.4;
  }

  60% {
    transform: scale(1.08);
    opacity: 1;
  }

  100% {
    transform: scale(1.0);
  }
}

.spawn2048 {
  animation: spawn2048-kf 140ms ease-out;
  /* must align with SPAWN_MS */
}

/* 2048 score effects */
@property --h {
  syntax: '<number>';
  initial-value: 0;
  inherits: false;
}

@keyframes hueSpin2048 {
  to { --h: 360; }
}

.animateHue2048 {
  color: hsl(var(--h) 85% 55%);
  animation: hueSpin2048 var(--hueDur, 2.4s) linear infinite;
}

@media (prefers-reduced-motion: reduce) { /* not applied if user prefers reduced motion */
  .animateHue2048 { animation: none; }
}
